function convertNumbers(data) {
  var numbers = {
    0: '০',
    1: '১',
    2: '২',
    3: '৩',
    4: '৪',
    5: '৫',
    6: '৬',
    7: '৭',
    8: '৮',
    9: '৯',
    '.': '.',
    '-': '-'
  };
  var result = '';

  if (!data) {
    return '';
  }

  var input = data.toString();
  var length = input.length;

  for (var i = 0; i < length; i++) {
    if (isNaN(parseFloat(input[i])) || isNaN(input[i] - 0)) {
      result += input[i];
    } else {
      result += numbers[input[i]];
    }
  }

  return result;
}

export default convertNumbers;